.data__wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @media (min-width: 768px) {
        flex-direction: row;
    }
}

.data__block {
    background-image: url("../img/svg/tel.svg");
    background-position: left center;
    background-repeat: no-repeat;
    margin-bottom: 30px;

    @media (min-width: 768px) {
        margin-bottom: 50px;
    }
}

.data__content {
    width: 100%;

    @media (min-width: 768px) {
        width: 60%;
    }
}

.data__caption {
    color: #1a6585;
    font-size: 28px;
}

.data__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.data__item {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    text-align: left;
    color: #fff;
    border: 1px solid #11465c;
    border-radius: 5px;
    background: linear-gradient(90deg, rgba(50,134,170,1) 60%, rgba(144,190,209,1) 83%, rgba(255,255,255,1) 100%);

    & span {
        display: block;
        height: 100%;
        width: 100% ;
        padding: 9px;

        @media (min-width: 768px) {
            padding: 10px 30px;
        }
    }

    & a {
        display: block;
        height: 100%;
        width: 100% ;
        padding: 9px;
        transition: all 0.5s;

        @media (min-width: 768px) {
            padding: 10px 30px;
        }

        &:hover {
            color: #1a6585;
            background-color: #fff;
        }
    }
}

.data__link {
    background-size: 30px;
    background-repeat: no-repeat;
    border-radius: 5px;
    background-position: center right 8px;

    @media (min-width: 768px) {
        background-position: center right 20px;
    }

    &--tg {background-image: url("../img/svg/tg.svg");}
    &--skype {background-image: url("../img/svg/skype.svg");background-size: 35px;}
    &--tel {background-image: url("../img/svg/phone.svg");}
    &--mail {background-image: url("../img/svg/mail.svg");}
    &--hh {background-image: url("../img/svg/hh.png");}
    &--vk {background-image: url("../img/svg/vk.svg");}
}

.data__ed-item {
    border-bottom: 2px solid #1a6585;
}

.data__year {
    font-weight: bold;
    font-size: 16px;
    font-style: italic;

    @media (min-width: 768px) {
        font-size: 20px;
    }
}

.data__institution {
    font-weight: bold;
    font-size: 18px;
    margin-top: 10px;
    @media (min-width: 768px) {
        font-size: 24px;
    }
}

.data__speciality {
    font-size: 18px;
}

.data__skill-list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    gap: 10px;

    .data__item {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        text-align: left;
        color: #fff;
        border: 1px solid #1a6585;
        background: rgb(62, 141, 174);
        border-radius: 5px;

        & span {
            padding: 9px;

            @media (min-width: 768px) {
                padding: 10px 10px;
            }
        }
    }
}
